import React, { useState } from 'react';
import { Redirect } from 'react-router-dom'
import RegisterFormComponent from './RegisterForm.component'
import { useMutation } from '@apollo/react-hooks'
import Loading from '../../../../components/Loading'
import { ADD_SESSION } from '../../../../schemas/session'

const fullScreen = () => {
    // Fullscreen mode
    let el = document.documentElement;
    let rfs = // for newer Webkit and Firefox
        el.requestFullscreen
        || el.webkitRequestFullScreen
        || el.mozRequestFullScreen
        || el.msRequestFullscreen;
    if (typeof rfs !== undefined && rfs) {
        rfs.call(el);
    }
}

const RegisterFormContainer = (props) => {
    const [open, setOpen] = useState(true);

    const [addSession, { loading }] = useMutation(ADD_SESSION, {
        onCompleted({ createSession }) {
            localStorage.setItem('session', createSession.session.uuid)
            setOpen(false)
            fullScreen()
        },
        ignoreResults: false
    })

    const handleSubmit = (data) => {
        const input = {
            email: data.email
        }

        addSession({
            variables: { input }
        })
    };

    if (!open) return (<Redirect to="/task" />)
    return (
        <div>
            {loading && (<Loading />)}
            <RegisterFormComponent
                handleSubmit={handleSubmit}
            />
        </div>
    )
}

export default RegisterFormContainer;


