// Register.jsx
import React from 'react';
import { Formik } from 'formik'

import Button from '@material-ui/core/Button';
import { FieldText } from '../../../../components/Form'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import { FormStyle, LiActions } from './RegisterFormStyle'

import RegisterFormSchema, { initialValues } from './RegisterForm.schema'

const classes = theme => ({
    spaceTop: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        justifyContent: 'center'
    }
});

const RegisterFormComponent = ({ handleSubmit }) => (
    <React.Fragment>
        <Dialog
            open={true}
            aria-labelledby="form-dialog-title"
        >
            <DialogContent>
                <div style={{ textAlign: 'justify' }}>
                    <h1 style={{ textAlign: 'center' }}>Welcome to Space Exploration!</h1>
                    <p>This is a very simple and fun game to play.</p>
                    <p>You are a space explorer looking for interplanetary treasures and you have just reached a new planet. You need to land your spaceship as close as possible to the treasure. But you only get to know the treasures exact location after landing! So the only tool you have is your own intuition.</p>
                    <p>You can freely rotate the planet and zoom. The more accurate you are, the better.</p>
                    <p>We strongly recommend that you use a mouse for a better experience with the game.</p>
                    <p>By double-clicking on the planets surface, you can define your landing spot and then the exact location of the treasure is revealed, along with a percentage score. The closer to 100% you reach, that means the closer to the treasure you've landed, and so the more accurate was your attempt.</p>
                    <p>Each game session is composed of 20 attempts, but you can do as many sessions as you wish. The more you play, the more you can practice your intuition!</p>
                    <p>By the end of each session, you will find your sessions score.The score is shown on table with the 20 distances from click to target. The last line of the table represents your final score. It ranges from 0 to 100. The higher the final score, the closer your clicks were to the target, overall.</p>
                    <p>Relax, and let your intuition guide you on each click!</p>
                    <p>Start now and have fun! </p>
                </div>
                <Grid container spacing={24}>
                    <Grid item xs={12}>
                        <Formik
                            initialValues={initialValues}
                            validationSchema={RegisterFormSchema}
                            onSubmit={data => handleSubmit(data)}
                        >
                            {props => (
                                <FormStyle>
                                    <FieldText
                                        {...props}
                                        fieldName="email"
                                        title='E-mail:'
                                    />
                                    <LiActions>
                                        <div style={{ textAlign: 'center', marginTop: 20, marginBottom: 40 }}>
                                            <Button variant="outlined" type="submit" color="default">
                                                Start
                                            </Button>
                                        </div>
                                    </LiActions>
                                </FormStyle>
                            )}
                        </Formik>
                    </Grid>
                </Grid>
                
                <DialogActions classes={{ root: classes.root }}>
                </DialogActions>
            </DialogContent>
        </Dialog>
    </React.Fragment>
)

export default withStyles(classes)(RegisterFormComponent);


