import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import RegisterForm from './RegisterForm';
import queryString from 'query-string';

window.qparsed = queryString.parse(window.location.search)

const styles = theme => ({
    list: {
        "& li:not(:last-child)": {
            marginBottom: '.8em'
        }
    },
    nobullets: {
        listStyle: 'none'
    },
    center: { 
        textAlign: 'center', 
        marginTop: 50 
    }
})

class Introduction extends Component {
    state = {
        open: true,
        formData: {}
    };

    handleIniciar = (formData) => {
        const nextState = 'tarefa';
        this.props.update(nextState, formData);
    }

    render() {
        return (
            <React.Fragment>
                <RegisterForm submit={this.handleIniciar}/>
            </React.Fragment>
        );
    }
}

export default withStyles(styles)(Introduction);


