import React, {useEffect} from 'react'
import Sphere from './Sphere.component'
import markerTarget from './assets/imgs/target.png'
import markerClick from './assets/imgs/click.png'

let earth = null
let markers = []

export default (props) => {
    const id = props.id || "earth_div"
    const { settings, initial, onMove, onDoubleClick } = props
    let answered = false
    const randomPos = initial;

    // Preloading markers
    useEffect( () => {
        const imageList = [markerClick, markerTarget]
        imageList.forEach((image) => {
            new Image().src = image
        })
        earth = null
    }, [])

    const mousemove = (e) => {
        if (e.latlng !== undefined)
            onMove(e.latitude, e.longitude);
    }

    const dblclick = (e) => {
        if (e.latlng !== undefined && !answered) {
            answered = true;
            var posClick = [e.latitude, e.longitude];
            markers.push(window.WE.marker(randomPos, markerTarget).addTo(earth));
            markers.push(window.WE.marker(posClick, markerClick).addTo(earth));
            //earth.setView(randomPos);
            earth.panTo(randomPos);
            console.log(randomPos)

            onDoubleClick(posClick[0], posClick[1]);
        }
    }

    const resetMap = () => {
        if (earth === null) {
            return false;
        }

        //earth.setView(settings.center);
        //earth.setZoom(2);
        //earth.setAltitude(20000000);
        earth.setAltitude(settings.altitude);
        earth.da.P.m.forEach((idx) => {
            var mk = earth.da.P.O[idx]
            mk.element.remove();
            mk.enabled = false;
            mk.visible = false;
        })
        markers = [];
        answered = false;
    }

    const openMap = () => {
        if (earth === null) {
            earth = new window.WE.map(id, settings);
            //const randomPos = extraOptions.initial;

            window.WE.tileLayer('https://rvsphere.s3.amazonaws.com/venus/{z}/{x}/{y}.png', {
                minZoom: 0,
                maxZoom: 5
            }).addTo(earth);
        } else {
            resetMap(settings);
        }

        // Adding events
        if (onMove) {
            earth.on('mousemove', mousemove);
        }
        earth.on('dblclick', dblclick);
    }

    useEffect(() => {
        openMap()
    }, [initial])

    return (
        <Sphere id={id} />
    )
}


