import React from 'react'
import { Sphere } from '../../../components/Sphere'
import Introduction from './Introduction'
import settings from '../sphere.settings'

export default () => (
    <React.Fragment>
        <Sphere
            id={"tmp"}
            initial={[0,0]}
            settings={settings}
        />
        <Introduction/>
    </React.Fragment>
)



