
import styled from 'styled-components'
import { Form } from 'formik'

export const FormStyle = styled(Form)`
  input {
    width: 100%;
  }
`

export const LiActions = styled.li`
  display:flex;
  flex-direction: row-reverse;
  > button { 
    margin-right: 0;
    margin-left:12px;
  }
`

export const ListFields = styled.div`
  margin:0;
  .rowList{
    display: flex;
  }
`



